.tab {
  @include poppins-text;
  position: relative;
  display: block;
  background: transparent;
  border: 0;
  text-align: left;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;

  &::before {
    @include pseudo(calc(50% - 8px), auto, 10px, 10px);
    right: 5.5%;
    border-top: 1px solid $main-theme-color;
    border-right: 1px solid $main-theme-color;
    transform: rotate(135deg);
    cursor: pointer;
  }

  &--open {
    &::before {
      transform: rotate(-45deg);
    }
  }

  @media (max-width: $tablet-width) {
    &::before {
      @include size(17px, 17px);
      top: calc(50% - 14px);
      right: 7.5%;
    }

    &--open {
      &::before {
        top: calc(50% - 4px);
      }
    }
  }

  @media (max-width: $mobile-width) {
    &::before {
      @include size(13px, 13px);
      top: calc(50% - 21px);
      right: 9%;
    }

    &--open {
      &::before {
        top: calc(50% - 14px);
      }
    }
  }
}

.tab-content--hide {
  display: none;
}
